            </swiper-slide>
          </swiper>
      </div>
   </m-card>
  </div>
</template>

<script>
export default {
  props:{
    title:{type:String,required:true},
    icon:{type:String,required:true},
    categories:{type:Array,}
  },
  data(){
    return{
      active:0,
    }
  }
}
